<template>
    <div>
        <h1>Edição de Gasto</h1>
        <hr>
        <div class="columns is-mobile">
        <div class="column is-half is-offset-one-quarter">
        <div v-if="error != undefined" >
         <div class="notification is-danger">
            <p>{{error}}</p>
         </div>
        </div>
       
        <label for="nome">Gasto:</label>
        <input type="text" placeholder="Nome do Usuário" class="input" v-model="gasto" > <br><br>
        <label for="nome">Valor:</label>
        <input type="email" placeholder="Digite seu e-mail" class="input" v-model="valor" > <br><br>
        <button class="button is-primary is-large" @click="update" >Editar</button>
    </div>
    </div>
    </div>
</template>

<script>
import axios from 'axios'
    
export default {
    created(){
    
        axios.get('http://localhost:8686/gastos/' + this.$route.params.id).then(res =>{
           console.log(res)

           this.gasto = res.data.gasto;
           this.valor = res.data.valor;
           this.id = res.data.id;


        }).catch(err =>{
            console.log(err.response)
            this.$router.push({name: 'registrogasto'})
        })
    },
    data(){
        return {
            gasto: '',
            valor: '',
            id: -1,
            error: undefined
        }
    },
    methods:{
        update(){
        axios.put("http://localhost:8686/gastos/",{
            gasto: this.gasto,
            valor: this.valor
        }).then(res =>{
            console.log(res)
            this.$router.push({name: 'edicaogasto'})
        }).catch(err => {
            var msgErro = err.response.data.err;
            this.error = msgErro;
        })
    }
    }
}
</script>

